import TronWeb from 'tronweb';
import BigNumber from 'bignumber.js';
import store from '../store';
import { showSnackbar } from '../actions/snackbar';
import { reloadAccount } from '../actions/app';
import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
} from '../components/Common/GameSnackbar';

import {
  FOUNDATION_ADDRESS,
  initContract,
  injectTronWeb,
  sunToTrx,
  TRON_WEB_STATE_LOCKED,
  TRON_WEB_STATE_NOT_INSTALLED,
  TRON_WEB_STATE_NOT_MAINNET,
  trxToSun,
  weiToToken,
  tokenToWei,
  getDateTimeFromUnix,
} from './tronCommon';

import { toTruncateDown } from './math';

const gasLimit = 50 * 1000000; // 50 trx

// MAINNET
const CONTRACT_P3X_ADDRESS = '41f9baacfd17022667ae3cce1bf6193fd6b0e201ef';
const TRONGRID_API = 'https://api.trongrid.io';


const initDefaultTronWeb = () => {
  const defaultTronWeb = new TronWeb(TRONGRID_API, TRONGRID_API, TRONGRID_API);

  // Set default address (foundation address) used for contract calls
  // Directly overwrites the address object as TronLink disabled the
  // function call
  defaultTronWeb.defaultAddress = {
    hex: defaultTronWeb.address.toHex(FOUNDATION_ADDRESS),
    base58: FOUNDATION_ADDRESS,
  };

  return defaultTronWeb;
};

export const defaultTronWeb = initDefaultTronWeb();

let currentAddress;
let contract_max;
let tronWebState;
const monitorAddressChanged = () => {
  window.tronWeb.on('addressChanged', async address => {
    if (currentAddress !== address.base58) {
      const needsToNotify = !!currentAddress;
      currentAddress = address.base58;

      tronWebState = await injectTronWeb(defaultTronWeb);
      contract_max = await initContract(
        tronWebState,
        defaultTronWeb,
        CONTRACT_P3X_ADDRESS
      );
      if (needsToNotify) {
        store.dispatch(showSnackbar('message.addressChanged', SNACKBAR_INFO));
        store.dispatch(reloadAccount());
      }
    }
  });
};

export default {
  async initTronWeb() {
    tronWebState = await injectTronWeb(defaultTronWeb);
    contract_max = await initContract(
        tronWebState,
        defaultTronWeb,
        CONTRACT_P3X_ADDRESS
      );
    if (!contract_max) {
      tronWebState = TRON_WEB_STATE_NOT_MAINNET;
      store.dispatch(
        showSnackbar('message.needToChangeNetwork', SNACKBAR_ERROR)
      );
    }

    monitorAddressChanged();

    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_INFO));
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_INFO));
    }
    return tronWebState;
  },

  getCurrentAddress() {
    return window.tronWeb.defaultAddress.base58;
  },

  // Main functions
  async currentAddressBalance() {
    if (tronWebState === TRON_WEB_STATE_LOCKED) {
      return 0;
    }
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  

  //P3X Token

  async getGlobalDataMAX() {
    const totalSupply = await contract_max.totalSupply().call();
    const buyPrice = await contract_max.buyPrice().call();
    const sellPrice = await contract_max.sellPrice().call();
    const globalDividends = await contract_max.globalDividends().call();
    return {
      totalSupply: totalSupply
        ? toTruncateDown(
            new BigNumber(weiToToken(new BigNumber(totalSupply).toNumber())),
            6
          )
        : 0,
      buyPrice: buyPrice ? sunToTrx(new BigNumber(buyPrice).toNumber()) : 0,
      sellPrice: sellPrice ? sunToTrx(new BigNumber(sellPrice).toNumber()) : 0,
      globalDividends: globalDividends
        ? sunToTrx(new BigNumber(globalDividends).toNumber())
        : 0,
    };
  },

  async buyPriceMAX() {
    const buyPrice = await contract_max.buyPrice().call();
    return sunToTrx(new BigNumber(buyPrice).toNumber());
  },

  async sellPriceMAX() {
    const sellPrice = await contract_max.buyPrice().call();
    return sunToTrx(new BigNumber(sellPrice).toNumber());
  },

  async buyReceiveMAX(amount) {
    return new Promise(async (resolve, reject) => {
      const trx = new BigNumber(trxToSun(amount));
      const hex = await window.tronWeb.toHex(trx.toNumber());
      try {
        const tokens = await contract_max.calculateTokensReceived(hex).call();
        resolve(toTruncateDown(new BigNumber(weiToToken(tokens)), 6));
      } catch (e) {
        reject(e);
      }
    });
  },

  async sellReceiveMAX(amount) {
    return new Promise(async (resolve, reject) => {
      const orcaes = new BigNumber(tokenToWei(amount));
      const hex = await window.tronWeb.toHex(orcaes.toNumber());
      try {
        const trx = await contract_max.calculateTronReceived(hex).call();
        resolve(sunToTrx(new BigNumber(trx)));
      } catch (e) {
        console.log(e);
        reject(e);
      }
    });
  },

  async getPlayerDataMAX(address) {
    if (!address || address === FOUNDATION_ADDRESS) {
      return {
        myTokens: 0,
        myDividends: 0,
        myReferrals: 0,
      };
    }

    const myTokens = await contract_max.myTokens().call();
    const myDividends = await contract_max.myDividends().call();
    // const myReferrals = await contract_max.myReferralReceived().call();

    return {
      myTokens: myTokens
        ? toTruncateDown(weiToToken(new BigNumber(myTokens).toNumber()), 6)
        : 0,
      myDividends: myDividends
        ? sunToTrx(new BigNumber(myDividends).toNumber())
        : 0,
    };
  },

  async balanceOfMAX(address) {
    const balance = await contract_max.balanceOf(address).call();
    return balance;
  },

  async purchaseMAX(sendAmount) {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = trxToSun(sendAmount);
    const data = await contract_max.buy().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async sellMAX(sendAmount) {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const orcaes = new BigNumber(tokenToWei(sendAmount));
    const hex = await window.tronWeb.toHex(orcaes.toNumber());

    const payAmount = 0;
    const data = await contract_max.sell(hex).send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async reinvestMAX() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;
    const data = await contract_max.reinvest().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async withdrawMAX() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;
    const data = await contract_max.withdraw().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  // Util functions
  fromHex(hex) {
    if (hex === '0x0000000000000000000000000000000000000000') {
      return '';
    } else {
      try {
        const base58 = defaultTronWeb.address.fromHex(hex.trim());
        return base58;
      } catch (e) {
        console.error(hex);
        console.error(e);
        return hex;
      }
    }
  },
};
