import React from 'react';
import { connect } from 'react-redux';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';

import Countdown from 'react-countdown-now';
import moment from 'moment';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import FileCopyIcon from '@material-ui/icons/FileCopy';

import { Translate, withLocalize } from 'react-localize-redux';
import siteTranslations from '../../translations/translate.json';

import {
  setScreen,
  SCREEN_VAULT,
} from '../../actions/app';

import { showSnackbar } from '../../actions/snackbar';

import customTheme from '../Common/customTheme';

import { TRON_WEB_STATE_NOT_MAINNET } from '../../utils/tronCommon';

import tron, { defaultTronWeb } from '../../utils/tronMainP3X';

import {
  SNACKBAR_ERROR,
  SNACKBAR_SUCCESS,
  SNACKBAR_INFO,
  SNACKBAR_WARNING,
} from '../Common/GameSnackbar';
import Tracker from '../../utils/tracker';
import { parseReferralAddress } from '../../utils/parseQueryString';
import GlobalStates from './GlobalStates';

const styles = theme => ({
  button: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
});

const shouldShowInvestor = true;

const WEB_SITE_URL = 'https://trondex.org';
const VERIFY_URL =
  'https://www.tronscan.org/#/contract/TYjetHL6xGS8FMW2B6FdDhzGBgLNLU8j96/code';

const gameStartTime = moment.utc('2020-11-04T11:00:00');
// Renderer callback with condition
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Start the game
    return null;
  } else {
    // Render a countdown
    return (
      <Grid item xs={12}>
        <span className="medium-text"><Translate id="max.gamestart" /></span>
        {/* <span className="xxl-text glowing-pink">{days}</span>{' '}
        <span className="medium-text">
          <Translate id="max.days" />{' '}
        </span>{' '} */}
        <span className="xxl-text glowing-pink">{hours}</span>{' '}
        <span className="medium-text">
          <Translate id="max.hours" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{minutes}</span>{' '}
        <span className="medium-text">
          <Translate id="max.minutes" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{seconds}</span>{' '}
        <span className="medium-text">
          <Translate id="max.seconds" />
        </span>{' '}
      </Grid>
    );
  }
};



const initialState = {
  totalSupply: null,
  buyPrice: null,
  sellPrice: null,
  buyAmount: '100',
  sellAmount: '0',
  buyReceiveTokens: '',
  sellReceiveTokens: '0',
  isLoading: false,
  myTokens: null,
  myDividends: null,
  myReferrals: null,
  accountValidForReferral: false,
  globalDividends: null,
  globalReferrals: null,
};

let loadDataInterval;
const INTERVAL = 3000; // 3 sec

class P3X extends React.Component {
  constructor(props) {
    super(props);

    this.state = initialState;
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    const tronWebState = await tron.initTronWeb();
    if (tronWebState === TRON_WEB_STATE_NOT_MAINNET) {
      return;
    }

    await this.loadData();
    clearInterval(loadDataInterval);
    loadDataInterval = setInterval(async () => {
      try {
        await this.loadData();
      } catch (e) {
        console.error(e);
        clearInterval(loadDataInterval);
      }
    }, INTERVAL);

    Tracker.pageview('/tvt');
  }

  componentWillUnmount() {
    clearInterval(loadDataInterval);
  }

  async componentDidUpdate(prevProps) {
    if (this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) {
      this.setState(initialState);
      await this.loadData();
    }
  }

  loadData = async () => {
    await this.loadTokenData();
  };

  loadTokenData = async () => {
    const { buyAmount, sellAmount } = this.state;
    await this.getGlobalData();
    await this.buyReceive(buyAmount);
    await this.sellReceive(sellAmount);
    await this.getPlayerData();
  };

  getGlobalData = async () => {
    const globalData = await tron.getGlobalDataMAX();
    if (globalData) {
      this.setState({
        totalSupply: globalData.totalSupply,
        buyPrice: globalData.buyPrice,
        sellPrice: globalData.sellPrice,
        globalDividends: globalData.globalDividends,
        globalReferrals: globalData.globalReferrals,
      });
    }
  };

  buyReceive = async amount => {
    if (amount > 0) {
      try {
        const buyReceiveTokens = await tron.buyReceiveMAX(amount);
        this.setState({ buyReceiveTokens });
      } catch (e) {
        console.error(e);
      }
    } else {
      this.setState({ buyReceiveTokens: 0 });
    }
  };

  sellReceive = async amount => {
    if (amount > 0) {
      try {
        const sellReceiveTokens = await tron.sellReceiveMAX(amount);
        this.setState({ sellReceiveTokens });
      } catch (e) {
        console.error(e);
      }
    } else {
      this.setState({ sellReceiveTokens: 0 });
    }
  };

  getPlayerData = async () => {
    const address = tron.getCurrentAddress();
    const accountBalance = await tron.currentAddressBalance();
    this.setState({
      accountValidForReferral: accountBalance > 0,
      accountBalance,
    });
    const playerData = await tron.getPlayerDataMAX(address);
    if (playerData) {
      const myTokens = playerData.myTokens;
      const myReferrals = playerData.myReferrals;
      const myDividends = playerData.myDividends;
      this.setState({
        myTokens,
        myDividends,
        myReferrals,
      });
    }
  };

  formatAmount = amount => {
    const formattedAmount = amount
      .replace(/^0+(?!\.|$)/, '')
      .replace(/[^0-9 .]+/g, '')
      .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1');
    if (formattedAmount.trim() === '') {
      return 0;
    } else {
      return formattedAmount;
    }
  };

  handleBuyAmountChange = async event => {
    const formattedAmount = this.formatAmount(event.target.value);
    this.setState({ buyAmount: formattedAmount });
    await this.buyReceive(formattedAmount);
  };

  handleSellAmountChange = async event => {
    let formattedAmount = this.formatAmount(event.target.value);
    this.setState({ sellAmount: formattedAmount });
    await this.sellReceive(formattedAmount);
  };


  navigateTo = screen => {
    const { setScreen } = this.props;
    this.setState({ mobileOpen: false });
    setScreen(screen);
  };

  dailydivs = () => {
    this.navigateTo(SCREEN_VAULT);
  };



  isButtonDisabled = () => {
    const { isLoading} = this.state;
    const gameStarted = gameStartTime.isBefore(new Date());
    return !!isLoading|| !gameStarted;
  };

  purchase = async () => {
    const { buyAmount } = this.state;
    const { showSnackbar } = this.props;
    Tracker.event({
      category: 'Token',
      action: 'Buy MAX',
    });
    if (buyAmount <= 0) {
      showSnackbar('message.buyNumberMustBePositive', SNACKBAR_WARNING);
      return;
    }
    this.setState({ isLoading: true });

    try {
      const result = await tron.purchaseMAX(buyAmount);
      this.setState({ isLoading: false });
      if (!result) {
        return;
      }
      showSnackbar('message.purchaseSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.error(e);
      this.setState({ isLoading: false });
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_WARNING);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.timout', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  sell = async () => {
    const { sellAmount, myTokens } = this.state;
    const { showSnackbar } = this.props;
    Tracker.event({
      category: 'Token',
      action: 'Sell MAX',
    });

    if (sellAmount <= 0) {
      showSnackbar('message.sellNumberMustBePositive', SNACKBAR_WARNING);
      return;
    }

    if (sellAmount > myTokens) {
      showSnackbar('message.sellMoreThanOwnedToken', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tron.sellMAX(sellAmount);
      if (!result) {
        return;
      }
      showSnackbar('message.sellSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.log(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_WARNING);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.timout', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  reinvest = async () => {
    const { myDividends } = this.state;
    const { showSnackbar } = this.props;
    Tracker.event({
      category: 'Token',
      action: 'Reinvest',
    });

    if (myDividends <= 0) {
      showSnackbar('message.reinvestNumberMustBePositive', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tron.reinvestMAX();
      if (!result) {
        return;
      }
      showSnackbar('message.purchaseSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.log(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_WARNING);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.timout', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  withdraw = async () => {
    const { myDividends } = this.state;
    const { showSnackbar } = this.props;
    Tracker.event({
      category: 'Token',
      action: 'Withdraw',
    });

    if (myDividends <= 0) {
      showSnackbar('message.withdrawNumberMustBePositive', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tron.withdrawMAX();
      if (!result) {
        return;
      }
      showSnackbar('message.withdrawSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.log(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_WARNING);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.timout', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  renderCountdownIfNeeded() {
    // const { loaded } = this.state;
    return (
      <Countdown date={gameStartTime.toDate()} renderer={renderer} />
    );
  }

  render() {
    const {
      totalSupply,
      buyPrice,
      sellPrice,
      globalDividends,
      globalReferrals,
      buyAmount,
      sellAmount,
      buyReceiveTokens,
      sellReceiveTokens,
      myTokens,
      myDividends,
      myReferrals,
      accountValidForReferral,
      accountBalance,
    } = this.state;
    const { classes } = this.props;
    const globalStates = {
      totalSupply,
      buyPrice,
      sellPrice,
      globalDividends,
      globalReferrals,
    };

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <GlobalStates globalStates={globalStates} />
          <div color="primary" className="card home-card">
            <div className="container">
              <Grid container spacing={24} className="component-card">
              <Grid item xs={12}>
                  <div className="jumbotron">
                    <Grid container spacing={24}>
                      <Grid item xs={12}>
                        <div className="xxl-size white">
                          <Translate id="maxtoken.maintitle" />
                        </div>
                        <div className="xxl-size white">
                          <Translate id="maxtoken.subtitle1" />
                        </div>

                        <div className="space" />

                        <div className="x-m-size grey">
                          <Translate id="maxtoken.subtitle2" />
                        </div>

                        <div className="x-m-size grey">
                          <Translate id="maxtoken.subtitle3" />
                        </div>
                      </Grid>
                      <Grid item xs={12}>
                        <Button
                          className="gta-button"
                          variant="contained"
                          disableRipple={true}
                          href={VERIFY_URL}
                          target="_blank"
                        >
                          <Translate id="max.verify" />{' '}
                          <Translate id="max.contract" />
                        </Button>
                      </Grid>
                    </Grid>
                  </div>
                </Grid>
                {this.renderCountdownIfNeeded()}
                <Grid item xs={12}>
                  <div className="m-size grey">
                    <img
                      src="images/misc/line.svg"
                      className="image-small"
                      alt="tron"
                    />
                  </div>
                </Grid>
                <div id="section_stats" />
                <Grid item xs={12} md={12}>
                  <Grid container spacing={24} className="content-card">
                    <Grid item xs={12} md={6}>
                      <div className="buy-placeholder">
                        <div id="token" className="title-placeholder dark-bg">
                          <span className="m-size gold">
                            <Translate id="max.buy" />
                          </span>
                        </div>
                        <div className="space" />
                        <div className="input-box">
                          <TextField
                            label=""
                            name="selectedAsset"
                            className="buy-token"
                            autoComplete="off"
                            value={buyAmount}
                            onChange={this.handleBuyAmountChange}
                            InputProps={{
                              endAdornment: (
                                <img
                                  src="images/misc/logo.svg"
                                  className="logo"
                                  alt="tron"
                                />
                              ),
                            }}
                            margin="normal"
                            variant="outlined"
                          />
                        </div>

                        <div>
                          <span className="s-size">
                            <span className="equal-sign">
                              = 
                            </span>{' '}
                            <span className="blue">{buyReceiveTokens}</span>{' '}
                            <span className="">
                              P3X
                            </span>
                          </span>
                        </div>
                        <Grid container spacing={24}>
                          <Grid item xs={12}>
                            <Button
                              id="buy-button"
                              className="invest-button"
                              variant="contained"
                              disableRipple={true}
                              disabled={this.isButtonDisabled()}
                              onClick={this.purchase}
                            >
                              <Translate id="max.buy" /> P3X
                            </Button>
                          </Grid>
                        </Grid>
                      </div>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <div className="buy-placeholder">
                        <div className="title-placeholder dark-bg">
                          <span className="m-size gold">
                            <Translate id="max.sell" />
                          </span>
                        </div>
                        <div className="space" />
                        <div className="input-box">
                          <TextField
                            label=""
                            name="selectedAsset"
                            className="sell-token"
                            autoComplete="off"
                            value={sellAmount}
                            onChange={this.handleSellAmountChange}
                            InputProps={{
                              endAdornment: (
                                <img
                                  src="images/misc/logo.svg"
                                  className="logo"
                                  alt="token"
                                />
                              ),
                            }}
                            margin="normal"
                            variant="outlined"
                          />
                        </div>
                        <div>
                          <span className="s-size">
                            <span className="equal-sign">
                              =
                            </span>{' '}
                            <span className="green">{sellReceiveTokens}</span>{' '}
                            <span>TRX</span>
                          </span>
                        </div>
                        <Grid container spacing={24}>
                          <Grid item xs={12}>
                            <Button
                              id="sell-button"
                              variant="contained"
                              className="withdraw-button"
                              disableRipple={true}
                              disabled={this.isButtonDisabled()}
                              onClick={this.sell}
                            >
                              <Translate id="max.sell" /> P3X
                            </Button>
                          </Grid>
                        </Grid>
                      </div>
                    </Grid>
                    <div className="space" />
                    <Grid
                      container
                      spacing={0}
                      className="component-card dd-card"
                    >
                      <Grid item xs={12} md={12}>
                        <div className="buy-placeholder">
                          <div className="my-account title-placeholder dark-bg">
                            <span className="m-size gold">
                              My Account
                            </span>
                          </div>
                          <div className="space" />
                          <Grid container spacing={24}>
                            <Grid item xs={6}>
                              <div>
                                <div className="middle-children gold">
                                  P3X <Translate id="max.token" />
                                </div>
                                <div className="space" />
                                <div>
                                  <span className="blue">{myTokens || 0}</span> P3X
                                </div>
                              </div>
                            </Grid>
                            <Grid item xs={6}>
                              <div>
                                <div className="middle-children">
                                  <img
                                    src="images/misc/logo.svg"
                                    className="logo"
                                    alt="logo"
                                  />{' '} <Translate id="max.withdrawableTrx" />
                                </div> 
                                <div className="space" />
                                <div>
                                  <span className="green">{myDividends || 0}</span> TRX
                                </div>
                              </div>
                            </Grid>
                          </Grid>
                          <Grid container spacing={0}>
                            <Grid item xs={12} md={6}>
                              <Grid container spacing={24}>
                                <Grid item xs={12}>
                                  <Button
                                    id="reinvest-button"
                                    variant="contained"
                                    className="invest-button"
                                    disableRipple={true}
                                    disabled={this.isButtonDisabled()}
                                    onClick={this.reinvest}
                                  >
                                    <Translate id="max.reinvest" />
                                  </Button>
                                </Grid>
                              </Grid>
                            </Grid>
                            <Grid item xs={12} md={6}>
                              <Grid container spacing={24}>
                                <Grid item xs={12}>
                                  <Button
                                    id="withdraw-button"
                                    variant="contained"
                                    className="withdraw-button"
                                    disableRipple={true}
                                    disabled={this.isButtonDisabled()}
                                    onClick={this.withdraw}
                                  >
                                    <Translate id="max.withdraw" />
                                  </Button>
                                </Grid>
                              </Grid>
                            </Grid>

                          </Grid>
                        </div>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                  <div className="m-size grey">
                    <img
                      src="images/misc/line.svg"
                      className="image-small"
                      alt="tron"
                    />
                  </div>
                </Grid>
              <Grid item xs={12} md={12}>
                  <div className="buy-placeholder faq-card">
                    <div className="my-account dark-bg">
                      <span className="m-size aqua">
                        <Translate id="maxtoken.about" />
                      </span>
                    </div>
                    <div className="space" />
                    <div className="faq-placeholder">
                      <div className="answer">
                         <Translate id="maxtoken.description" />
                      </div>
                    </div>
                  </div>
                </Grid>

              <div className="space" />
            </div>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapStateToProps = state => ({
  reloadAccountNonce: state.app.reloadAccountNonce,
});

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withLocalize(withStyles(styles)(P3X)));
